import { chat_metadata, saveSettingsDebounced } from '@sillytavern/script';
import { extension_settings, saveMetadataDebounced } from '@sillytavern/scripts/extensions';

interface VariableOption {
  type: 'chat' | 'global'; //  (`'chat'`)  (`'global'`) ,  `'chat'`
}

function getVariablesByType(type: 'chat' | 'global'): Record<string, any> {
  switch (type) {
    case 'chat': {
      const metadata = chat_metadata as {
        variables: Record<string, any> | undefined;
      };
      if (!metadata.variables) {
        metadata.variables = {};
      }
      return metadata.variables;
    }
    case 'global':
      return extension_settings.variables.global;
  }
}

/**
 * 
 *
 * @param option 
 *   - `type?:'chat'|'global'`:  (`'chat'`)  (`'global'`) ,  `'chat'`
 *
 * @returns 
 *
 * @example
 * // 
 * const variables = getVariables();
 * alert(variables);
 *
 * @example
 * // 
 * const variables = getVariables({type: 'global'});
 * //  lodash , , 
 * if (_.has(variables, ".")) {
 *   ...
 * }
 */
export function getVariables(option: VariableOption = { type: 'chat' }): Record<string, any> {
  const { type = 'chat' } = option;
  const result = getVariablesByType(type);

  console.info(`$${type == 'chat' ? `` : ``}:\n${JSON.stringify(result, undefined, 2)}`);
  return result;
}

/**
 *  `variables`
 *
 * ,  lodash :
 *   `insertOrAssignVariables`  `getVariables` ,  lodash ,  `replaceVariables` .
 *
 * @param variables 
 * @param option 
 *   - `type?:'chat'|'global'`:  (`'chat'`)  (`'global'`) ,  `'chat'`
 *
 * @example
 * // : `{: {: 5}}`
 * await replaceVariables({: {: 5, : 0}});
 * // : `{: {: 5, : 0}}`
 *
 * @example
 * //  `{: {: 5}}` 
 * let variables = getVariables();
 * _.unset(variables, ".");
 * replaceVariables(variables);
 */
export function replaceVariables(variables: Record<string, any>, option: VariableOption = { type: 'chat' }): void {
  const { type = 'chat' } = option;
  switch (type) {
    case 'chat':
      (chat_metadata as { variables: Object }).variables = variables;
      saveMetadataDebounced();
      break;
    case 'global':
      extension_settings.variables.global = variables;
      saveSettingsDebounced();
      break;
  }

  console.info(`${type == 'chat' ? `` : ``}:\n${JSON.stringify(variables, undefined, 2)}`);
}

type VariablesUpdater =
  | ((variables: Record<string, any>) => Record<string, any>)
  | ((variables: Record<string, any>) => Promise<Record<string, any>>);

/**
 *  `updater` 
 *
 * @param updater . , .
 * @param option 
 *   - `type?:'chat'|'global'`:  (`'chat'`)  (`'global'`) ,  `'chat'`
 *
 * @returns 
 *
 * @example
 * //  `{: {: 5}}` 
 * await updateVariablesWith(variables => {_.unset(variables, "."); return variables;});
 *
 * @example
 * //  "."  2 ,  0
 * await updateVariablesWith(variables => _.update(variables, ".", value => value ? value * 2 : 0));
 */
export async function updateVariablesWith(updater: VariablesUpdater, option: VariableOption = { type: 'chat' }): Promise<Record<string, any>> {
  const { type = 'chat' } = option;
  let variables = await getVariables({ type });
  variables = await updater(variables);
  console.info(`[Chat Message][updateVariablesWith] ${type === 'chat' ? `` : ``}`);
  replaceVariables(variables, option);
  return variables;
}

/**
 * , .
 *
 * @param variables 
 *   - , 
 *   - , 
 * @param option 
 *   - `type?:'chat'|'global'`: ,  'chat'
 *
 * @example
 * // : `{: {: 5}}`
 * await insertOrAssignVariables({: {: 10}, : {: 5, : 0}});
 * // : `{: {: 10}, : {: 5, : 0}}`
 */
export async function insertOrAssignVariables(variables: Record<string, any>, option: VariableOption = { type: 'chat' }): Promise<void> {
  const { type = 'chat' } = option;
  await updateVariablesWith(old_variables => _.merge(old_variables, variables), { type });
}

/**
 * , 
 *
 * @param variables 
 *   - , 
 *   - , 
 * @param option 
 *   - `type?:'chat'|'global'`: ,  'chat'
 *
 * @example
 * // : `{: {: 5}}`
 * await insertVariables({: {: 10}, : {: 5, : 0}});
 * // : `{: {: 5}, : {: 5, : 0}}`
 */
export async function insertVariables(variables: Record<string, any>, option: VariableOption = { type: 'chat' }): Promise<void> {
  const { type = 'chat' } = option;
  await updateVariablesWith(old_variables => _.defaultsDeep(old_variables, variables), { type });
}

/**
 * , 
 *
 * @param variable_path 
 *   - , 
 *   - , 
 * @param option 
 *   - `type?:'chat'|'global'`: ,  'chat'
 *
 * @returns 
 *
 * @example
 * // : `{: {: 5}}`
 * await deleteVariable(".");
 * // : `{: {}}`
 */
export async function deleteVariable(variable_path: string, option: VariableOption = { type: 'chat' }): Promise<boolean> {
  const { type = 'chat' } = option;
  let result: boolean = false;
  await updateVariablesWith(old_variables => { result = _.unset(old_variables, variable_path); return old_variables; }, { type });
  return result;
}